.btn-sub-table{
	width: 17px;
	height: 17px;
	font-size: 10px;
	padding: 0px 0px;
}


.sub-table
{
	background-color: #fff !important;
	display:none;
	font-size: 12px;
	
	.badge-boughtout{
		display:none;
	}

	table{
		width:100%;
	}

	.btn{
		width: 17px;
		height: 17px;
		font-size: 10px;
		padding: 0px 0px;
	}

	td{
		background-color: #fff !important;
		padding: 4px 5px;
	}
	
	.buyout
	{
		color: red;
		text-decoration: line-through;
		
		.badge-boughtout{
			display:block;
			text-decoration: none;
		}
		
	}
	
}

